import Vue from 'vue';
import { sleep } from '../commons';

/**
 * Metodos utilitários globais.
 */
Vue.mixin({
    methods: {
        $alert(msg) {
            this.$store.commit('ui/ALERT', msg);
        },

        $notify(msg) {
            this.$store.commit('ui/NOTIFY', msg);
        },

        async $sleep(ms) {
            return sleep(ms);
        },
    },
});
