import Vue from 'vue';
import Router from 'vue-router';
import { routes as docs } from '../plugins/xtpgui';
import store from './store';
import domains from '../domains/routes';

Vue.use(Router);

/**
 * Evite alterações neste arquivo, adicione suas rotas
 * em src/routes.js sempre que necessário.
 */
const router = new Router({
    mode: 'history',
    base: process.env.BASE_URL,
    routes: [
        ...docs,
        ...domains,
    ]
});

router.beforeEach(async (to, from, next) => {
    if (!to.meta.middleware || (to.meta.middleware && !to.meta.middleware.length)) {
        return next();
    }

    const middleware = Array.isArray(to.meta.middleware) ? to.meta.middleware : [to.meta.middleware];
    const context = { from, to, next, router, store };
    const nextMiddleware = nextFactory(context, middleware, 1);
    return middleware[0]({ ...context, next: nextMiddleware });
});

function nextFactory(context, middleware, index) {
    const subsequentMiddleware = middleware[index];

    if(!subsequentMiddleware){
        return context.next;
    }

    return (...parameters) => {
        context.next(...parameters);
        const nextMiddleware = nextFactory(context, middleware, index + 1);
        subsequentMiddleware({ ...context, next: nextMiddleware });
    };
}

export default router;
