import { http, sleep } from '@/plugins/xtpgui';

export const info = async() => {
    return http('docs').get('/api/version');
}

export const login = async(credential) => {
    const res = await http('docs').post('/api/login', credential);

    if (res.status !== 200) {
        throw new Error(res.data.message);
    }

    window.sessionStorage.setItem('sid', res.data.token);

    return res.data;
}

export const logout = async(credential) => {
    const res = await http('docs').get('/api/logout');

    if (res.status === 200) {
        window.sessionStorage.removeItem('sid');
    }
}

export const checkToken = async() => {
    const res = await http('docs').get('/api/token');

    if (res.status !== 200) {
        throw new Error(res.data.message);
    }

    return res.data;
}

export default {
    info,
    login,
    logout,
    checkToken,
}
