export function isNull(val) {
    if (val === undefined || val === null || val === '') {
        return true;
    }
    return false;
}

export function isNotNull(val) {
    if (val !== undefined && val !== null && val !== '') {
        return true;
    }
    return false;
}

export function recursiveMerge(obj1, obj2) {
    for (const [key, value] of Object.entries(obj2)) {
        if (Object.prototype.toString.call(value) === '[object Object]' && value !== null) {
            obj1[key] = obj1[key] ? {...obj1[key]} : {};
            recursiveMerge(obj1[key], value);
        } else {
            obj1[key] = value;
        }
    }
    return obj1;
}

export function sleep(ms, cb) {
    return new Promise(resolve => {
        setTimeout(() => {
            if (cb) cb();
            resolve();
        }, ms)
    });
}

export default {
    isNull,
    isNotNull,
    recursiveMerge,
    sleep,
}
