import themes from './libs/vuetify/themes';
import components from './views/data/components';
import { folders } from './views/data/structures';
import { lua } from './libs/vuetify/themes';
import { sleep } from './commons';

export default {
    namespaced: true,

    state: {
        theme: lua,

        themes,

        message: {
            alert: {
                title: 'message.alert.title',
                text: null,
                visible: false,
            },
            notify: {
                text: null,
                visible: false,
                color: 'warning',
                timeout: 5000
            },
        },

        docs: {
            components,
            folders,
        },
    },

    mutations: {
        SET_THEME(state, theme) {
            state.theme = theme;
        },
        ALERT(state, msg) {
            if (typeof msg === 'object') {
                return state.message.alert = {
                    ...state.message.alert,
                    ...msg
                };
            }
            state.message.alert.text = msg;
            state.message.alert.visible = true;
        },
        NOTIFY(state, msg) {
            if (typeof msg === 'object') {
                return state.message.notify = {
                    ...state.message.notify,
                    ...msg
                };
            }
            state.message.notify.text = msg;
            state.message.notify.visible = true;
        },
    },

    actions: {},
};
